import { Component, Inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatDialogModule, MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';

export interface ConfirmDialogData {
  message: string;
  title?: string;
  confirmText?: string;
  cancelText?: string;
  type?: 'warning' | 'danger' | 'info';
}

/**
 * ÎîîÎ≤ÑÍ∑∏ Î°úÍ∑∏ Ï∂úÎ†• (window.debugLogger ÏÇ¨Ïö©)
 */
function debugLog(category: string, message: string): void {
  const logger = (window as any).debugLogger;
  if (logger) {
    logger.info(category, message);
  } else {
    console.warn(`[${category}] ${message}`);
  }
}

@Component({
  selector: 'app-confirm-dialog',
  standalone: true,
  imports: [
    CommonModule,
    MatDialogModule,
    MatButtonModule,
    MatIconModule
  ],
  template: `
    <div class="confirm-dialog">
      <div class="dialog-header" [class]="'type-' + data.type">
        <mat-icon class="dialog-icon">
          {{ getIcon() }}
        </mat-icon>
        <h2 mat-dialog-title>{{ data.title || 'ÌôïÏù∏' }}</h2>
      </div>
      
      <mat-dialog-content>
        <p class="dialog-message">{{ data.message }}</p>
      </mat-dialog-content>
      
      <mat-dialog-actions align="end">
        <button 
          mat-stroked-button 
          (click)="onCancel()"
          class="cancel-btn">
          {{ data.cancelText || 'Ï∑®ÏÜå' }}
        </button>
        <button 
          mat-flat-button 
          [color]="getButtonColor()"
          (click)="onConfirm()"
          class="confirm-btn"
          cdkFocusInitial>
          {{ data.confirmText || 'ÌôïÏù∏' }}
        </button>
      </mat-dialog-actions>
    </div>
  `,
  styles: [`
    .confirm-dialog {
      min-width: 320px;
      max-width: 500px;
    }

    .dialog-header {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 20px 24px 16px;
      border-bottom: 1px solid #e0e0e0;

      &.type-warning {
        background: linear-gradient(135deg, #fff3e0 0%, #ffffff 100%);
        
        .dialog-icon {
          color: #f57c00;
        }
      }

      &.type-danger {
        background: linear-gradient(135deg, #ffebee 0%, #ffffff 100%);
        
        .dialog-icon {
          color: #d32f2f;
        }
      }

      &.type-info {
        background: linear-gradient(135deg, #e3f2fd 0%, #ffffff 100%);
        
        .dialog-icon {
          color: #1976d2;
        }
      }

      .dialog-icon {
        font-size: 32px;
        width: 32px;
        height: 32px;
      }

      h2 {
        margin: 0;
        font-size: 20px;
        font-weight: 600;
        color: #212121;
      }
    }

    mat-dialog-content {
      padding: 24px;
      
      .dialog-message {
        margin: 0;
        font-size: 15px;
        line-height: 1.6;
        color: #424242;
        white-space: pre-wrap;
      }
    }

    mat-dialog-actions {
      padding: 16px 24px;
      gap: 12px;

      .cancel-btn {
        min-width: 80px;
        border-width: 2px;
      }

      .confirm-btn {
        min-width: 80px;
        font-weight: 600;
      }
    }
  `]
})
export class ConfirmDialogComponent {
  constructor(
    public dialogRef: MatDialogRef<ConfirmDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ConfirmDialogData
  ) {
    debugLog('ConfirmDialog', 'üîç ÏÉùÏÑ±Ïûê Ìò∏Ï∂úÎê®');
    debugLog('ConfirmDialog', `üîç data: ${JSON.stringify(this.data)}`);
    
    // Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
    this.data.type = this.data.type || this.detectType(this.data.message);
    
    debugLog('ConfirmDialog', `üîç ÌÉÄÏûÖ ÏÑ§Ï†ï ÏôÑÎ£å: ${this.data.type}`);
    debugLog('ConfirmDialog', `üîç dialogRef ÏÉÅÌÉú: id=${this.dialogRef.id}, disableClose=${this.dialogRef.disableClose}`);
  }

  getIcon(): string {
    switch (this.data.type) {
      case 'danger': return 'warning';
      case 'warning': return 'info';
      case 'info': return 'help_outline';
      default: return 'help_outline';
    }
  }

  getButtonColor(): 'primary' | 'warn' {
    return this.data.type === 'danger' ? 'warn' : 'primary';
  }

  detectType(message: string): 'warning' | 'danger' | 'info' {
    const lowerMessage = message.toLowerCase();
    if (lowerMessage.includes('ÏÇ≠Ï†ú') || lowerMessage.includes('Ï†úÍ±∞')) {
      return 'danger';
    }
    if (lowerMessage.includes('Í≤ΩÍ≥†') || lowerMessage.includes('Ï£ºÏùò') || lowerMessage.includes('ÎÇòÍ∞Ä')) {
      return 'warning';
    }
    return 'info';
  }

  onConfirm(): void {
    debugLog('ConfirmDialog', '‚úÖ onConfirm() Ìò∏Ï∂úÎê®');
    debugLog('ConfirmDialog', 'dialogRef.close(true) Ìò∏Ï∂ú...');
    this.dialogRef.close(true);
    debugLog('ConfirmDialog', 'dialogRef.close(true) ÏôÑÎ£å');
  }

  onCancel(): void {
    debugLog('ConfirmDialog', '‚ùå onCancel() Ìò∏Ï∂úÎê®');
    debugLog('ConfirmDialog', 'dialogRef.close(false) Ìò∏Ï∂ú...');
    this.dialogRef.close(false);
    debugLog('ConfirmDialog', 'dialogRef.close(false) ÏôÑÎ£å');
  }
}
